.filter-param__active{
	width: 100%;
	display: flex;
	flex-wrap: wrap;

	div{
		font-size: 14px;
		line-height: 26px;
		background: #E9F1F3;
		border-radius: 2px;
		display: flex;
		align-items: center;
		margin-right: 15px;
		padding-left: 15px;
		cursor: pointer;
		transition: .4s;
		border: 1px solid transparent;
		color: black;
		svg{
			margin: 11px 15px 11px 15px;
			width: 11px;
			height: 11px;
			transition: .4s;
			fill: #B0BABA;
		}
		&:hover{
			border-radius: 2px;
			background-color: #d6dee0;
			//border-color: #18a0fb;
			svg{
				fill: #858c8c;
			}
		}
	}
}
.catalog-filter{
	margin-bottom: 40px;
	&__top{
		background-color: #E9F1F3;
		border-top: 1px solid #D0D7D8;
		border-bottom: 1px solid #D0D7D8;
		position: relative;
		z-index: 50;
		.container{
			display: flex;
			justify-content: space-between;
		}
		.param{
			display: flex;
			align-items: center;

		}
		&-text{
			font-weight: 300;
			font-size: 16px;
			line-height: 1.6;
			color: #000000;
			text-transform: uppercase;
			.mob{
				display: none;
			}
		}
		&-btn{
			padding: 12px 46px 12px 24px;
			background-color: #F8FCFD;
			font-weight: 600;
			font-size: 16px;
			line-height: 26px;
			margin: 0 20px;
			position: relative;
			cursor: pointer;
			color: #353535;
			svg{
				width: 10px;
				position: absolute;
				right: 21px;
				height: 10px;
				top: 16px;
				transition: .3s;
			}
			.ar{
				transform: rotate(90deg);
			}
			.close{
				opacity: 0;
				width: 12px;
				height: 12px;
				fill: #F74B4B;
				right: 20px;
				top: 18px;
			}
			span{
				border-bottom: 1px dotted black;
			}
		}
		&-btn.open{
			background: #FFFFFF;
			box-shadow: 0px 0px 25px #C3CDD0;
			border-top-left-radius: 4px;
			border-top-right-radius: 4px;
			span{
				border-color: transparent;
			}
			.ar{
				opacity: 0;
			}
			.close{
				opacity: 1;
			}
		}
	}
	&__bottom{
		position: relative;
		z-index: 10;
		background: #F4F8F9;
		overflow: hidden;
		.jq-checkbox{
			width: 13px;
			height: 13px;
			background: #DCE5E7;
			border-radius: 2px;
			border: 0;
			box-shadow: none;
			transition: .3s;
			margin-right: 15px;
		}
		.jq-checkbox.checked .jq-checkbox__div {
			width: 8px;
			height: 5px;
			border-bottom: 1px solid #fff;
			border-left: 1px solid #fff;
		}
		.jq-checkbox.checked{
			background-color: #F64B4B;
		}
		label{
			display: flex;
			align-items: center;
			cursor: pointer;
			span{
				font-size: 14px;
				line-height: 155%;
				font-weight: 400;
				color: #636367;
				max-width: calc(100% - 30px);
			}
		}
		.catalog-filter--sbmt{
			margin-left: 80px;
		}
		&-param{
			background: #FFFFFF;
			box-shadow: 0px 0px 25px #C3CDD0;
			margin-bottom: 40px;
			display: none;
		}
		&-rasroch{
			.catalog-filter__btns{
				justify-content: center;
				.catalog-filter--sbmt{
					margin-left: 60px;
					min-width: 350px;
				}
			}
		}
	}
	&__maker{
		&-zag{
			padding: 12px 35px;
			border-bottom: 1px solid #E4E9EA;
			font-weight: 600;
			font-size: 16px;
			line-height: 26px;
			color: #353535;
		}
		&-block{
			padding: 20px 35px;
			border-bottom: 1px solid #E4E9EA;
			ul{
				display: flex;
				flex-flow: column wrap;
				max-height: 170px;
				li{
					width: 25%;
					font-size: 14px;
					line-height: 15px;
					color: #353535;
					margin-bottom: 10px;
				}
				.country{
					display: flex;
					align-items: center;
					font-weight: 600;
					font-size: 15px;
					line-height: 15px;
					color: #353535;
					margin: 10px 0 15px;
					svg{
						width: 19.5px;
						height: 13px;
						margin-right: 8.5px;
					}
					img{
						width: 19.5px;
						height: 13px;
						margin-right: 8.5px;
					}
				}
			}
			ul.maxWidth{
				overflow: auto;
				li{
					width: 15%;
				}
			}
		}
	}
	&__container{
		display: flex;
		flex-wrap: wrap;
		padding: 30px 10px;
	}
	&__block{
		width: 33.33%;
		padding:  15px 25px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		> div{
			width: 270px;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			position: relative;
		}
		.jq-selectbox ul{
			max-height: 200px;
			overflow-y: auto;
		}
		span{
			font-weight: 600;
			font-size: 16px;
			line-height: 155%;
			letter-spacing: -0.02em;
			color: rgba(22, 22, 22, 0.8);
		}
		> span{
			max-width: 50%;
			width: 100%;
			padding-right: 10px;
		}
	}
	&__input{
		input{
			width: 50%;
			border: 1px solid #8D9B9E;
			box-shadow: none;
			padding: 5px 13px;
			box-sizing: border-box;
			font: inherit;
			&:first-child{
				border-top-left-radius: 2px;
				border-bottom-left-radius: 2px;
			}
			&:last-child{
				border-top-right-radius: 2px;
				border-bottom-right-radius: 2px;
				border-left: 0;
			}
			&::placeholder{
				font-size: 14px;
				line-height: 155%;
				letter-spacing: -0.02em;
				color: rgba(22, 22, 22, 0.35);
			}
		}
	}
	&__select{
		.jq-selectbox__dropdown{
			margin-top: 39px;
			z-index: 999;
		}
		.jq-selectbox__select-text{
			font-size: 14px;
			line-height: 37px;
			letter-spacing: -0.02em;
			color: rgba(22, 22, 22, 0.35);
		}
		.jq-selectbox{
			width: 100%;
		}
		.jq-selectbox__select{
			padding-left: 15px;
			height: 37px;
			width: 100%;
			box-shadow: none;
			background: none;
			font-family: "montserat";
			border-radius: 2px;
			border: 1px solid #8D9B9E !important;
		}
		.jq-selectbox li{
			font-family: "montserat";
			white-space: inherit;
			border-bottom: 1px solid rgba(3, 3, 3, 0.1);
		}
		.jq-selectbox__trigger-arrow{
			display: none;
		}
		.jq-selectbox__trigger{
			border: 0;
			background-size: 10px;
			background-position: 10px 10px;
			background-repeat: no-repeat;
			background-image: url(../images/svg/a-select.svg);
			transition: 0.3s;
		}
		.jq-selectbox li.selected {
			background-color: #e9f1f3;
			color: #363636;
		}
		.jq-selectbox li:hover {
			background-color: #f4f8f9;
			color: #232323;
		}
	}
	&__check{
		.jq-checkbox{
			width: 13px;
			height: 13px;
			background: #DCE5E7;
			border-radius: 2px;
			border: 0;
			box-shadow: none;
			transition: .3s;
			margin-right: 15px;
		}
		.jq-checkbox.checked .jq-checkbox__div {
			width: 8px;
			height: 5px;
			border-bottom: 1px solid #fff;
			border-left: 1px solid #fff;
		}
		.jq-checkbox.checked{
			background-color: #F64B4B;
		}
		label{
			display: flex;
			align-items: center;
			cursor: pointer;
			span{
				font-size: 14px;
				line-height: 155%;
				font-weight: 400;
				color: #636367;
			}
		}
	}
	&__variant{
		.filter__variant__block{
			width: auto;
			position: relative;
			z-index: 1;
			&-name{
				font-weight: 500;
				font-size: 16px;
				line-height: 155%;
				letter-spacing: -0.02em;
				background: #FFFFFF;
				transition: .3s;
				padding: 7px 65px 7px 17px;
				background-image: url(../images/svg/ar-ri-black.svg);
				background-position: right 10px center;
				background-repeat: no-repeat;
				position: relative;
				z-index: 3;
				cursor: pointer;
				span{
					position: relative;
					z-index: 9;
				}
				&::after{
					content: '';
					height: 100%;
					width: 50%;
					position: absolute;
					top: 0;
					right: 10px;
					background-image: url(../images/svg/ar-ri-black.svg);
					background-position: right 10px center;
					background-repeat: no-repeat;
				}
				&::before{
					content: '';
					height: 100%;
					width: 100%;
					position: absolute;
					top: 0;
					left: 10px;
					background-color: #fff;
				}
				span{
					border-bottom: 1px dotted black;
				}

			}
			&-ul{
				position: absolute;
				width: 280px;
				top: calc(100% + 10px);
				right: 0;
				padding: 15px;
				background-color: #fff;
				border-bottom-left-radius:  5px;
				border-bottom-right-radius:  5px;
				box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.12);
				transition: .3s;
				opacity: 0;
				visibility: hidden;
				z-index: 800;
				max-height: 270px;
				overflow-y: auto;
				li{

				}
			}
			&:hover{
				.filter__variant__block-name{
					box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.12);
					span{
						color: #F64B4B;
					}
				}
				.filter__variant__block-ul{
					right: 0%;
					top: 100%;
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
	&__btns{
		padding: 40px 35px;
		display: flex;
		align-items: center;
		border-top: 1px solid #E4E9EA;
	}
	&__rasr{
		max-width: 640px;
		margin: 0 auto;
		&-zag{
			font-weight: 600;
			font-size: 22px;
			line-height: 26px;
			color: #353535;
			padding: 45px 0 60px;
			text-align: center;
		}
		.catalog-filter__block{
			width: 100%;
			margin-bottom: 15px;
			padding-left: 0;
			padding-right: 0;
			justify-content: flex-start;
			span{
				text-align: right;
				width: calc(100% - (350px + 175px));
				&:first-child{
					text-align: left;
					width: 175px;
				}
			}

			> div{
				width: 350px;
			}
		}
		.catalog-filter__block.single{
			input{
				width: 100%;
				border: 1px solid #8D9B9E;
			}
			.absolute{
				position: absolute;
				right: 10px;
				width: auto;
				top: 5px;
				font-size: 14px;
			}
		}
		.reset{
			background: none;
			margin: 0 auto 30px;
			width: 320px;
			&:hover{
				color: #F74B4B;
				background: none;
			}
		}
	}
}
.sort{
	display: flex;
	align-items: center;
	font-weight: 300;
	font-size: 16px;
	line-height: 26px;
	color: #313636;
	.jq-selectbox__select{
		background: none;
		border: 0 !important;
		box-shadow: none;
		font-family: "montserat";
		font-weight: 300;
		padding-right: 35px;
	}
	.jq-selectbox__select-text{
		font-size: 16px;
	}
	.jq-selectbox__trigger{
		border: 0;
		background-size: 10px;
		background-position: 10px;
		background-repeat: no-repeat;
		background-image: url(../images/svg/a-r-black.svg);
		transform: rotate(90deg);
		transition: 0.3s;
	}
	.jq-selectbox__trigger-arrow{
		display: none;
	}
	.jq-selectbox__dropdown{
		margin: 9px 0 0;
		padding: 0;
		border-radius: 0;
		background: #FFF;
		box-shadow: none;
		font: inherit;
		right: 0;
		min-width: 150px;
	}
	.jq-selectbox li:hover {
		background-color: #e9f1f3;
		color: #838a8a;
	}
	.jq-selectbox li.selected {
		background-color: #e9f1f3;
		color: #232323;
	}
	.jq-selectbox.opened{
		.jq-selectbox__trigger{
			transform: rotate(-90deg);
		}
	}
}
.presence--btn,
.reset{
	padding: 10px 17px;
	background: #E9F1F3;
	border-radius: 2px;
	display: flex;
	align-items: center;
	font-weight: 600;
	font-size: 14px;
	line-height: 34px;
	color: #353535;
	margin-right: 30px;
	border: 0;
	cursor: pointer;
	transition: .4s;
	svg{
		fill: #F74B4B;
		width: 14px;
		height: 14px;
		margin-right: 15px;
	}
	.jq-checkbox{
		margin-right: 20px;
		border: 0 !important;
		box-shadow: none;
		background-color: #fff;
		width: 21px;
		height: 21px;
		background-color: #fff;
	}
	.jq-checkbox.checked{
		background-color: #fff;
	}
	.jq-checkbox.checked .jq-checkbox__div{
		width: 14px;
		height: 14px;
		background-image: url(../images/svg/check-red.svg);
		background-size: contain;
		background-repeat: no-repeat;
		margin: 3px;
		border: 0;
		transform: rotate(-7deg);
	}
	&:hover{
		background-color: #d6dee0;
		svg{
			fill: #858c8c;
		}
	}
	span{
		font-weight: 600 !important;
		font-size: 14px !important;
		line-height: 34px !important;
		color: #353535 !important;
	}
}
.reset.for--mob{
	display: none;
}
.catalog-filter--sbmt{
	font-weight: 600;
	font-size: 14px;
	line-height: 32px;
	text-transform: uppercase;
	color: #353535;
	padding: 9px 40px;
	border: 2px solid #F74B4B;
	border-radius: 2px;
	background: none;
	transition: .5s;
	cursor: pointer;
	&:hover{
		background-color:  #F74B4B;
		color: #fff;
	}
}
.tooltip-span{
	position: relative;
	z-index: 80;
	padding-right: 15px;
	.quest{
		color: #fff;
		width: 11px;
		height: 11px;
		background-color: #F74B4B;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 8px;
		border-radius: 50%;
		position: absolute;
		right: 0;
		top: 0;
		line-height: 1.3;
	}
	.tooltip-ico{
		padding: 0 5px;
		width: 120px;
		left: 100%;
		line-height: 1.5;
		transform: translateX(-30px);
		text-align: center;
		span{
			font-size: 10px;
			line-height: 13px;
			color: #5C5C5C;
			text-align: center;
			display: flex;
			&::before{
				left: 13px;
			}

		}

	}
	&:hover{
		z-index: 200;
		.tooltip-ico{
			opacity: 1;
			visibility: visible;
			z-index: 10;
			top: 23px;
		}
	}
}


@media screen and(max-width: 600px){
	.catalog-filter__bottom label{
		width: 100%;
	}
}